import React from "react";
import {
  TiSocialFacebook,
  TiSocialTwitter,
  TiSocialInstagram,
  TiSocialGooglePlus,
  TiSocialPinterest,
  TiSocialYoutube
} from "react-icons/ti";
function Icons() {
  return (
    <ul className="links-container">
      <li className="icon-link">
        <a href="#">
          <TiSocialFacebook />
        </a>
      </li>
      <li className="icon-link">
        <a href="#">
          <TiSocialTwitter />
        </a>
      </li>
      <li className="icon-link">
        <a href="#">
          <TiSocialInstagram />
        </a>
      </li>
      <li className="icon-link">
        <a href="#">
          <TiSocialGooglePlus />
        </a>
      </li>
      <li className="icon-link">
        <a href="#">
          <TiSocialPinterest />
        </a>
      </li>
      <li className="icon-link">
        <a href="#">
          <TiSocialYoutube />
        </a>
      </li>
    </ul>
  );
}

export default Icons;
