import React from "react";

import { MdKeyboardArrowRight } from "react-icons/md";
import { CgArrowRight } from "react-icons/cg";
function Banner() {
  return (
    <div className="banner-container">
      <div className="content">
        <p class="campaign-title">WINTER IN THE PARK</p>
        <a
          href="https://www.gucci.com/us/en/st/stories/advertising-campaign/article/gucci-beloved-winter-in-the-park"
          className="campaign-link"
        >
          Disover the campaign <MdKeyboardArrowRight className="c-icon" />
        </a>
      </div>
    </div>
  );
}

export default Banner;
